html, body {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;
}

.mode {
  transition: .25s ease-out; 
}

.light-mode {
  background-color: #fff;
  color: #000;
}

.dark-mode {
  background-color: #1a1a1a;
}

.dark-mode .notes-header {
  color: #fff;
}

.container {
  max-width: 1200px;
  min-height: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.notes-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.toggle {
  position: relative;
  width: 60px;
  height: 30px;
  border-radius: 15px;
  background-color: #eee;
  cursor: pointer;
  transition: .25s ease-out;
}

.toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(20%);
  border-radius: 50%;
  background-color: #000;
  width: 20px;
  height: 20px;
  transition: .1s ease-in;
}

.checkbox-mode {
  display: none;
}

.checkbox-mode:checked + .toggle::after {
  transform: translateY(-50%) translateX(180%);
  background-color: #fff;
}

.checkbox-mode:checked + .toggle {
  background-color: #000;
}





.notes-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
}

.note {
  background-color: #f1dd6b;
  border-radius: 10px;
  padding: 1rem;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

.note.new {
  background-color: #67d7cc;
}

textarea {
  resize: none;
  border: none;
  outline: none;
  background-color: transparent;
}

.note-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
}

.note-footer > .save {
  background-color: #e1e1e1;
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  cursor: pointer;
}

.del-icon {
  cursor: pointer;
}

.search {
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 10px;
  background-color: rgb(233,233,233);
  padding: 15px;
  margin-bottom: 20px;
}

.search > input {
  width: 100%;
  border: none;
  outline: none;
  background-color: transparent;
}

.search__icon {
  cursor: pointer;
  padding: 5px;
}

.remove-searchText__icon {
  padding: 5px;
  opacity: 0;
  transition: .4s ease-out;
}
.remove-searchText__icon--open {
  opacity: 1;
  cursor: pointer;
}